import { TABLES } from "../types"

export const editCell = (id, col, value) => ({
	type: TABLES.EDIT_CELL,
	payload: {
		type: "CHANGE",
		payload: {
			id,
			col,
			value
		}
		
	}
})

export const saveDataFromTables = (url, method) => ({
	type: TABLES.SAVE_DATA,
	payload: {url, method}
})