import React from "react"

import UniversalTable from "./UniversalTable"
import { 
	getBackups, 
	getColors, 
	getMaterials, 
	makeBackup, 
	saveColors, 
	saveMaterials,
	toBackup
} from "../api"

import { 
	Form, 
	Button,
	ButtonToolbar,
	ButtonGroup,
} from "react-bootstrap"

import {
	FaChevronLeft,
	FaChevronRight
} from "react-icons/fa"

// Styles
import "../styles/SysSettings.scss"
import { NotificationManager } from "react-notifications"



export default class SystemSetting extends React.Component{
	constructor(props){
		super(props)

		this.state = {
			backups: [],

			// pagination
			countItemsInPage: 10,
			currentPage: 1,
			maxCountPages: 5
		}

		this.columnColors = [
			{
				text: 'ID',
				dataField: 'id',
				sort: true
			},
			{
				text: 'Цвет',
				dataField: 'color',
			}
		]
		this.columnMaterials = [
			{
				text: 'ID',
				dataField: 'id',
				sort: true
			},
			{
				text: 'Материал',
				dataField: 'material',
			}
		]
	}

	handleMakeBackup(e){
		makeBackup((data) => {
			this.setState({
				backups: [data.fileName, ...this.state.backups]
			})
		}, (err) => console.log(err))
	}

	toBackup(backupName){
		window.confirm("Вы уверены, что хотите откатиться?") && toBackup(backupName, 
			data => !data.error && NotificationManager.success("База данных успешно откатилась", "OK", 3000), 
			err => NotificationManager.error("Не удалось откататиться", "Ошибка", 3000))
	}

	// --------------------------------------------------------------------------
	// ------------------------- Pagination Backups -----------------------------
	// --------------------------------------------------------------------------


	range(size, startAt = 0) {
		return [...Array(size).keys()].map(i => i + startAt);
	}
	toForward(e){
		this.state.currentPage < Math.ceil(this.state.backups.length / this.state.countItemsInPage) && 
			this.setState({ currentPage: this.state.currentPage + 1 })
	}
	toBackward(e){ this.state.currentPage > 1 && this.setState({ currentPage: this.state.currentPage - 1 }) }
	
	pickPage(page){
		this.setState({ currentPage: page })
	}

	componentDidMount(){
		getBackups((data) => {
			this.setState({ backups: data.data.reverse() })
		})
	}

	render(){
		const backups = 
			this.state.backups.filter((_, index) => 
				index >= (this.state.currentPage - 1) * this.state.countItemsInPage && 
				index < (this.state.currentPage) * this.state.countItemsInPage)

		// panigation
		const countPages = Math.ceil(this.state.backups.length / this.state.countItemsInPage)

		return(
			<div className="SystemSettings">
				<div className="slot slot_colors">
					<UniversalTable
						className="colors"
						title="Цвета"
						columns={this.columnColors}

						getDataFromAPI={getColors}
						saveDataToAPI={saveColors}

						optPagination={true}
						enableNewRow={true}
					/>
				</div>
				<div className="slot slot_materials">
					<UniversalTable
						className="materials"
						title="Материалы"
						columns={this.columnMaterials}

						getDataFromAPI={getMaterials}
						saveDataToAPI={saveMaterials}

						optPagination={true}
						enableNewRow={true}
					/>
				</div>
				<div className="slot slot_backups">
					<h2>База данных</h2>
					
					<p>Бэкапы <button className="btn btn-primary" onClick={e => this.handleMakeBackup(e)}>Сделать бэкап</button></p>
					<ul className="slot_backups_list">
						{
							backups.map(backupName => {
								return <li className="backups_item">
									<span className="backups_item_filename">{backupName.split(".").filter((_, index) => index < 5).join(".")}</span>
									<button className="btn btn-danger" onClick={e => this.toBackup(backupName)}>Откатить</button>
									</li>
							})
						}
					</ul>

					<div className="pagination">
							<Form.Select onChange={e => this.setState({ countItemsInPage: +e.target.value })} className="header-selects header-selects-year" aria-label="Выберете айтомов">
								<option value="10" selected>Кол-во: 10</option>
								<option value="20">Кол-во: 20</option>
								<option value="30">Кол-во: 30</option>
								<option value="40">Кол-во: 40</option>
								<option value="50">Кол-во: 50</option>
							</Form.Select>

							<ButtonToolbar aria-label="Toolbar with button groups" className="pagination-pages">
								<ButtonGroup className="me-2" aria-label="First group">
									<Button onClick={this.toBackward.bind(this)}><FaChevronLeft /></Button>
								</ButtonGroup>
								<ButtonGroup className="me-2" aria-label="Second group">
									{
										this.range(countPages).map((_, index) => {
											return <Button onClick={e => this.pickPage(index + 1)} active={this.state.currentPage === index + 1}>{index + 1}</Button>
										})
									}
								</ButtonGroup>
								<ButtonGroup aria-label="Third group">
									<Button onClick={this.toForward.bind(this)}><FaChevronRight /></Button>
								</ButtonGroup>
							</ButtonToolbar>
						</div>
				</div>
				{/* Colors table */}
				
			</div>
		)
	}
}