import { NotificationManager } from "react-notifications"

import { APP } from "../types"
import { sendInvoicesChanges } from "../../api"

const initialStateApp = {
	authorization: true,
	invoicesChanges: []
}

export const app = (state = initialStateApp, action) => {
	switch(action.type){
		case APP.AUTH:
			return {
				...state,
				authorization: action.payload
			}
		case APP.LOGOUT:
			localStorage.removeItem("token")
			return {
				...state,
				authorization: action.payload
			}
		case APP.INVOICE_ADD_CHANGE:
			// check with contains change in stack invoices' changes
			let containsIndex = -1
			state.invoicesChanges.map((change, index) => { 
				if(change.type === "CHANGE" &&
					change.payload.col === action.payload.payload.col && 
					change.payload.id === action.payload.payload.id)
						return (containsIndex = index)
			})
			console.log(containsIndex, ~containsIndex === 0)
			if(~containsIndex === 0)
				return {
					...state,
					invoicesChanges: [...state.invoicesChanges, action.payload]
				}
			else
				return {
					...state,
					invoicesChanges: state.invoicesChanges.reduce(
						(prev, curr, index) => { 
							return index !== containsIndex? [...prev, curr]: [...prev, action.payload]
						},
						[]
					)
				}


			
		case APP.INVOICE_SAVE_CHANGES:
			sendInvoicesChanges(action.payload, state.invoicesChanges, res => {
				if(res.error) NotificationManager.warning("Кажется, ошибка на сервере", "Ошибка", 3000)
				else NotificationManager.success("Данные успешно сохранены", "ОК", 3000)
			}, err => NotificationManager.warning("Кажется, ошибка на сервере", "Ошибка", 3000))
			return {
				...state,
				invoicesChanges: []
			}
		default:
			return state
	}
}